<template>
  <div class="col-md-6 product-component">
    <div class="product-inner-wrapper panel ">
      <small>{{ x_ase_rel }} // {{ x_ase_current_page }}</small>
      <div class="thumbnail">
        <img class="thumbnail-img"
             :src="page_config.image_base + product.image + '?serial=6' ">

        <!--src="https://mir-s3-cdn-cf.behance.net/project_modules/disp/ce54bf11889067.562541ef7cde4.png">-->

        <div class="caption">
          <div class="product-wrapper">
            <div class="product-detail">
              <h4> name: </h4>
              <div class="product-name">
                <h3>{{ product.name }}</h3>
              </div>
            </div>
            <div class="product-detail"><h4> price:</h4>
              <p>{{ product.price }} </p></div>
            <div class="product-detail"><h4> description:</h4>
              <div>
                <span class="product-id">product-id: {{product.id}}</span>
                // <span>owner: {{product.user_id}}</span>
              </div>
              <p class="product-description">{{ product.description }} </p>
            </div>
          </div>
          <hr/>

          <p>
            <router-link
              :to="'/products/' +product.id + '/edit'"
              class="btn btn-success"
            >
              Edit
            </router-link>

            <a href="#" class="btn btn-danger" role="button"
               v-if="product.user_id===p_current_user.id"
               @click="$emit('delete-product')">
              Delete
            </a>

            <a href="#" class="btn btn-success">
              buy
            </a>

          </p>
          <hr/>
          <p>
            <a href="#" class="btn btn-default">
              wish list
            </a>


          </p>


        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: [
      'p_current_user',
      'product',
      'x_ase_rel', 'x_ase_current_page'],
    data () {
      return {
        current_page: -1,
        page_config: {
          rogera_original_port: 8000,
          image_base: 'http://sms-back-dev.base.wolfspool.at/images/',
        },
      }
    },
  }
</script>
<style>
  .product-inner-wrapper {
    /*margin: 4px;*/
    background-color: lightgray;

  }

  .product-component {
    /*background-color: cornflowerblue;*/
    /*border: thin navy solid;*/
    /*padding: 4px;*/
  }

  .product-name h3 {
    /*line-height: 1.1em;*/

    padding: 4px;

    min-height: 3.3em;
    max-height: 3.3em;
    height: 3.3em;

    overflow: auto;
    background-color: aliceblue;
  }

  .product-description {
    /*line-height: 1.1em;*/

    padding: 4px;

    min-height: 6.6em;
    max-height: 6.6em;
    height: 6.6em;

    overflow: auto;
    background-color: aliceblue;
  }

  .product-wrapper {
    /*background-color: cadetblue;*/
    /*margin-right: 6px;*/
  }

  .thumbnail {
    /*background-color: #aaf4f9*/
  }

  .thumbnail-img {
    height: 120px !important;
  }

  .product-detail {
    /*border: 1px dashed #080808;*/
    margin: 6px;
    padding: 6px;
    /*background-color: white;*/
  }
</style>



