<template>
  <div>
    <div id="project-container">
      <div v-for="project in projects">
        project: zdl {{ project }}
      </div>
    </div>

    <div id="process-description">
      <section class="marketing"><p>
        bootstrap.css von der cdn abkopiert: https://www.bootstrapcdn.com/
        (quickstart)</p>
      </section>
      <section class="marketing"><p>
        jumbotron.css mit chrome-devtools anzeigen lassen, c/p vvv hier unten vvv in
        das script eingefuegt</p>
      </section>
    </div>
  </div>
</template>
<script>
  export default {
    components: {},
    data () {
      return {
        projects: [
          {name: 'wprcs', location: '/var/www'},
          {name: 'front', location: '/home/rogera/code/devlob/front'},
          {name: 'back', location: '/home/rogera/code/devlob/back'},
          {name: 'back', location: '/home/rogera/code/devlob/back'},
        ],
      }
    },
  }
</script>



