<template class="container">
  <div class="row header clearfix">
    <h3 class="text-muted col-md-5">system management service</h3>
    <nav class="col-md-7 pull-right">
      <ul class="nav nav-pills pull-right">

        <router-link to="/cockpit"
                     tag="li"
                     v-if="isAuth">
          <a>cockpit</a>
        </router-link>
        <router-link to="/layer-1"
                     tag="li"
                     v-if="isAuth">
          <a>layer 1</a>
        </router-link>
        <router-link to="/layer-2"
                     tag="li"
                     v-if="isAuth">
          <a>layer 2</a>
        </router-link>

        <router-link to="/feed" tag="li" v-if="isAuth">
          <a>feed</a></router-link>

        <router-link to="/products/create"
                     tag="li"
                     v-if="isAuth">
          <a>create product</a>
        </router-link>

        <router-link to="/logout" tag="li" v-if="isAuth">
          <a>logout</a></router-link>

        <router-link to="/login" tag="li" v-if=" ! isAuth">
          <a>login</a>
        </router-link>

        <router-link to="/register" tag="li" v-if=" ! isAuth">
          <a>register</a></router-link>


      </ul>
    </nav>
  </div>
</template>
<script>
  import {EventBus} from '../services/event-bus'

  export default {
    data () {
      return {
        isAuth: null,
      }
    },
    created () {
      this.isAuth = this.$auth.isAuthenticated()
      this.setAuthenticatedUser()
      EventBus.$emit('notification', {notification: 'navbar component created'})
    },
    beforeDestroy () {
      EventBus.$emit('notification', {notification: 'navbar component beforeDestroy'})
      // EventBus.$off()
    },
    methods: {
      setAuthenticatedUser () {
        this.$http.get('api/user')
          .then(
            response => {
              this.$auth.setAuthenticatedUser(response.body)
              console.log(this.$auth.getAuthenticatedUser())
            })
      },
    },
  }
</script>

