<template class="">
  <div class="container-fluid" id="app">
    <div class="content">
      <!--<div class="header">dev-dd-ww /</div>-->
      <backbone class="navbar-fixed-top"></backbone>
      <navbar></navbar>
      <router-view></router-view>
      <!--<projects></projects>-->
    </div>
    <div class="footer">
      <!--<div class="jumbotron"><span class="btn"></span>-->
      <!--</div>-->
      <span class="copyright-text navbar-fixed-bottom">&copy;-left ase</span>
    </div>
  </div>
</template>

<script>

  import Backbone from './components/s2222/Backbone.vue'
  import Navbar from './components/Navbar.vue'
  import Projects from './components/scratch/Projects'

  export default {
    name: 'App',
    created () {
    },
    components: {
      'backbone': Backbone,
      'navbar': Navbar,
      'projects': Projects,
    },
    data () {
      return {
        name: 'jumbotron marketing',
      }
    },
  }
</script>

<style lang="scss">
  /* Space out content a bit */
  body {
    padding-top: 24px;
  }

  /* Everything but the jumbotron gets side spacing for mobile first views */
  .header,
  .marketing,
  .footer {
    padding-right: 15px;
    padding-left: 15px;
  }

  .footer {
    /*position: fixed;*/
    /*height: 100px;*/
    /*bottom: 0;*/
    /*width: 100%;*/

    .copyright-text {
      background-color: #67a869;
      color: #eef6dc;
      padding: 16px;
    }
  }

  /* Custom page header */
  .header {
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 3px;
  }

  /* Make the masthead heading the same height as the navigation */
  .header h3 {
    margin-top: 0;
    margin-bottom: 0;
    line-height: 40px;
  }

  /* Custom page footer */
  .footer {
    margin-top: 3px;
    padding-top: 19px;
    color: #777;
    border-top: 1px solid #e5e5e5;
  }

  /* Customize container */
  @media (min-width: 768px) {
    .container {
      /*max-width: 730px;*/
      /*max-width: 1024px;*/
    }
  }

  .container-narrow > hr {
    margin: 30px 0;
  }

  /* Main marketing message and sign up button */
  .jumbotron {
    text-align: center;
    border-bottom: 1px solid #e5e5e5;
  }

  .jumbotron .btn {
    padding: 14px 24px;
    font-size: 21px;
  }

  /* Supporting marketing content */
  .marketing {
    margin: 40px 0;
  }

  .marketing p + h4 {
    margin-top: 28px;
  }

  /* Responsive: Portrait tablets and up */
  @media screen and (min-width: 768px) {
    /* Remove the padding we set earlier */
    .header,
    .marketing,
    .footer {
      padding-right: 0;
      padding-left: 0;
    }

    /* Space out the masthead */
    .header {
      margin-bottom: 30px;
    }

    /* Remove the bottom border on the jumbotron for visual effect */
    .jumbotron {
      border-bottom: 0;
    }
  }

</style>



