<template class="container">
  <div>
    <h3>layer-2</h3>
    <my-gp-suche
      :connect-version="0.1"
      :ts="new Date()"
    ></my-gp-suche>

  </div>
</template>
<script>
  import {EventBus} from '../services/event-bus'
  import GpSuche from './cosmo/GpSuche'

  export default {
    components: {
      'my-gp-suche': GpSuche,
    },
    created () {
      EventBus.$emit('notification', {notification: 'layer-2 component created'})
    },
    beforeDestroy () {
      EventBus.$emit('notification', {notification: 'layer-2 component beforeDestroy'})
      //            EventBus.$off()
    },
  }
</script>



