import Vue from 'vue'
import ioClient from 'socket.io-client'

export const EventBus = new Vue()
let io = ioClient('http://metaloi.chickenkiller.wolfspool.at:2222/')
io.on('connect', function (socket) {
  io.emit('helo', {data: 'event-bus vue app connected'})
  io.on('event', function (data) {
    console.log('event-bus got data-event w/ data: ' + JSON.stringify(data))
  })
  EventBus.$on('notification', function (data) {
    // b-cast onto s2222
    io.emit('helo', {data: 'event-bus vue notification', payload: data})
  })
})



