<script>
  import {EventBus} from '@/services/event-bus'

  var controlSection = (context, section) => {
    'use strict'
    console.log(context.currentSection)

    context.species = 'section selected: ' + section

    console.log('setCurrentView to ' + section)
    context.currentSection = section
  }

  export default {

    // should set 'name'
    // template: '#template-connect',
    name: 'CockpitMain',

    props: ['connect_version', 'ts'],
    beforeDestroy () {
      EventBus.$emit('notification', {notification: 'cockpit-main component beforeDestroy'})
      //            EventBus.$off()
    },
    data: function () {
      return {
        currentSection: 'section-2',
        section1: {
          subtitle: 'CSECT.STD: ONE',
        },
        section2: {
          subtitle: 'CSECT oxygen system',
        },
        tempSection: 0,
        mouseLocation: 'outside',
        version: '1.6',
        dogs: [{
          name: 'Dennis',
          specialties: 'Computers, lost children, squirrels',
          price: 50,
        }, {
          name: 'Daisy',
          specialties: 'Butt smells',
          price: 70,
        }],
        cats: [{
          name: 'Mimi',
          specialties: 'Feathers, mice, 20th century art theft',
          price: 200,
          cares: false,
        }, {
          name: 'Sammy',
          specialties: 'Is actually a dog',
          price: 2,
          cares: true,
        }],
        searchTerm: '',
        systemComponentName: 'cockpitMain',
        species: 'none yet',
        computedResult: 'not yet',
      }
    },

    created: function () {
      var self = this
      console.log('created: ' + self)
      //                if (null == self.props.ts) {
      //                    self.props.ts = "[created] " + Date()
      //                }
      //                if (null == self.props.connect_version) {
      //                    self.props.connect_version = "in created"
      //                }
      EventBus.$emit('notification', {notification: 'cockpit-main component created'})
    },

    mounted: function () {
      var self = this
      console.log('mounted: ' + self)
    },

    computed: {
      searchPlaceholder: function () {
        'use strict'
        let self = this
        return 'Find a psychic ' + self.species + '.'
      },
      computedReference: function () {
        this.computedResult = Date()
        return this.computedResult
      },
      results: function () {
        'use strict'
        var self = this
        self.searchTerm = ''
        if (self.species === 'dog') {
          return self.dogs
        }

        if (self.species === 'cat') {
          return self.cats
        }
      },

      filteredResults: function () {
        'use strict'
        var self = this
        return self.results.filter(function (psychic) {
          return psychic.name.toLowerCase().indexOf(self.searchTerm.toLocaleLowerCase()) !== -1
        })
      },

      computedTs: function () {
        var self = this
        if (self.ts == null) {
          console.log('computedTs')
          return '[computedTs] ' + Date()
        } else return self.ts
      },

      computedConnectVersion: function () {
        var self = this
        if (self.connect_version == null) {
          console.log('computedConnectVersion')
          return '[in computedConnectVersion]'
        } else return self.connect_version
      },
    },

    methods: {
      changeClassMouseout (event) {
        this.mouseLocation = 'outside'
        controlSection(this, this.tempSection)
        // this.currentSection = this.tempSection
        console.log('mouse out')
      },
      changeClassMouseenter (event) {
        this.mouseLocation = 'inside'
        this.tempSection = this.currentSection
        controlSection(this, 'section-4')

        console.log('mouse enter')
      },
      setCurrent1: function (event) {
        event.preventDefault()
        console.log(controlSection(this, 'section-1'))
      },
      setCurrent2: function (event) {
        event.preventDefault()
        controlSection(this, 'section-2')
      },
      setCurrent3: function (event) {
        event.preventDefault()
        controlSection(this, 'section-3')
      },

      handleDogs: function (event) {
        'use strict'
        var self = this
        event.preventDefault()
        console.log('handleDogs')
        self.species = 'dog'
      },

      handleCats: function (event) {
        'use strict'
        var self = this
        event.preventDefault()
        console.log('handleCats')
        self.species = 'cat'
      },

      take_gp: function (gp) {
        'use strict'
        var self = this
        //                    if (event) event.preventDefault()
        self.searchTerm = gp
        console.log('take_gp: ' + gp)
        var AseGpEl = document.getElementById('ase_gp')
        if (AseGpEl !== null) {
          AseGpEl.value = gp
        } else {
          console.log('NOP take_gp: no parent AseGpEl')
        }
      },
    },
  }


</script>

<style lang="scss">
  div {
    /*border: thin #eef6dc dashed*/
  }

  .section {
    min-height: 100% !important;
  }

  .demo-div-1 {
    min-height: 96px;
    background-color: #c4d5d7;
    padding: 8px;
  }

  #section-3-sample {
    /* Set height of the grid so .sidenav can be 100% (adjust as needed) */
    .row.content {
      height: 480px
    }

    /* Set gray background color and 100% height */
    .sidenav {
      background-color: #c4d5d7;
      height: 100%;
    }

    /* On small screens, set height to 'auto' for the grid */
    @media screen and (max-width: 767px) {
      .row.content {
        height: auto;
      }
    }
  }

</style>

<ignore>
  <head>
    <title>Bootstrap Example</title>
