<template class="">
  <div class="row">
    <h3>feed</h3>
    <my-products></my-products>
  </div>
</template>
<script>
  import {EventBus} from '../../services/event-bus'
  import Products from '../product/Products.vue'

  export default {
    components: {
      'my-products': Products,
    },
    created () {
      EventBus.$emit('notification', {notification: 'feed component created'})
    },
    beforeDestroy () {
      EventBus.$emit('notification', {notification: 'feed component beforeDestroy'})
      //            EventBus.$off()
    },
  }
</script>



