import Vue from 'vue'
import VueRouter from 'vue-router'
import Cockpit from '@/components/cockpit/cockpit-main'
import Feed from '@/components/products/Feed'
import Layer1 from '@/components/Layer-1'
import Layer2 from '@/components/Layer-2'
import ProductCreate from '../components/product/Create'
import ProductEdit from '@/components/product/Edit'
import Login from '@/components/authentication/Login'
import Logout from '@/components/authentication/Logout'
import Register from '@/components/authentication/Registration'
// import VeeValidate, {Validator} from 'vee-validate'
import VeeValidate from 'vee-validate'
import messagesDe from 'vee-validate/dist/locale/de'

const config = {
  errorBagName: 'errors', // change if property conflicts.
  fieldsBagName: 'fields',
  delay: 0,
  locale: 'de',
  dictionary: null,
  strict: true,
  enableAutoClasses: false,
  classNames: {
    touched: 'touched', // the control has been blurred
    untouched: 'untouched', // the control hasn't been blurred
    valid: 'valid', // model is valid
    invalid: 'invalid', // model is invalid
    pristine: 'pristine', // control has not been interacted with
    dirty: 'dirty', // control has been interacted with
  },
  events: 'input|blur',
  inject: true,
}
// Add locale helper.
VeeValidate.Validator.localize('de', messagesDe)
Vue.use(VeeValidate, config)

Vue.use(VueRouter)

// let router = new VueRouter
// router.map({
//     '/logout': {
//         component: require('./App.vue')
//     }
// })
// export default router

export default new VueRouter({
  routes: [
    // {
    //   path: '*',
    //   component: require('./components/authentication/Login.vue'),
    //   meta: {
    //     forVisitors: true,
    //   },
    // },
    {
      path: '/cockpit',
      component: Cockpit,
      name: 'CockpitMain',
      meta: {forAuth: true},
    },
    {
      path: '/feed',
      component: Feed,
      meta: {forAuth: true},
    },
    {
      path: '/layer-1',
      component: Layer1,
      meta: {forAuth: true},
    },
    {
      path: '/layer-2',
      component: Layer2,
      meta: {forAuth: true},
    },
    {
      path: '/products/create',
      component: ProductCreate,
      meta: {
        forAuth: true,
      },
    },
    {
      path: '/products/:product/edit',
      component: ProductEdit,
      meta: {
        forAuth: true,
      },
    },
    {
      path: '/login',
      component: Login,
      meta: {
        forVisitors: true,
      },
    },
    {
      path: '/logout',
      component: Logout,
      meta: {
        forAuth: true,
      },
    },
    {
      path: '/register',
      component: Register,
      meta: {
        forVisitors: true,
      },
    },
  ],
  linkActiveClass: 'active',
  mode: 'history',
})






