<template class="">
  <div class="row">
    <span class="col-md-6"> {{ name }}  by {{ author }} </span>
    <span class="col-md-2 pull-right"> {{ ts }}</span>
  </div>
</template>
<script>
  import {EventBus} from '../../services/event-bus'

  export default {
    data () {
      return {name: 'backbone viewer', author: '@wolfhesse', ts: Date.now()}
    },
    created () {
      EventBus.$on('notification', data => {
        data.notification = data.notification ? data.notification : 'empty'
        this.ts = Date.now()
        console.info('ASE: //Backbone/got notification: ' + data.notification)
      })
    },
    beforeDestroy () {
      EventBus.$emit('notification', {notification: 'backbone-viewer: EOD'})
      EventBus.$off()
    },
  }
</script>
<style scoped>
  div {
    background-color: #67a869;
    color: #eef6dc;
    padding: 8px;
  }
</style>



