Vue.use(Auth)
Vue.use(BootstrapVue)
Vue.use(VueCookie)
Vue.cookie.set('XDEBUG_SESSION', 'PHPSTORM')
Vue.use(VueResource)
Vue.http.options.root = 'http://sms-back-test.base.wolfspool.at'
Vue.http.headers.common['Authorization'] = 'Bearer ' + Vue.auth.getToken()
Vue.http.interceptors.push((request, next) => {
  next(response => {
    if (response.status === 404) {
      swal(response.status.toString(), response.body.error, 'error')
    } else if (response.status === 500) {
      swal(response.status.toString(),
        'We are experiencing a problem with our server\n\n'
        // + response.body.error,
        , 'error')
    }
  })
})

Router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.forVisitors)) {
    if (Vue.auth.isAuthenticated()) {
      // todo maybe dashboard for sms
      next({path: '/feed'})
    } else next()
  } else if (to.matched.some(record => record.meta.forAuth)) {
    if (!Vue.auth.isAuthenticated()) {
      next({path: '/login'})
    } else next()
  } else next()
})

/* eslint-disable no-new */
var vm = new Vue({
  el: '#app',
  components: {App},
  template: '<App/>',
  router: Router,
})

vm.$watch('render', (newValue, oldValue) => {
  console.log('vm render chaned from / to:' + oldValue + ' ' + newValue)
})



