<template>
  <div class="row">
    <div class="col-md-8 col-md-offset-1">
      <div class="panel panel-default">
        <div class="panel-body">
          <!--<form>-->
          <div class="form-group">
            <label>Name:</label>
            <input type="text" class="form-control" title="product name"
                   v-model="product.name">
          </div>
          <div class="form-group">
            <label>Price:</label>
            <input type="number" class="form-control" title="product price"
                   v-model="product.price">
          </div>
          <div class="form-group">
            <label>Description:</label>
            <textarea rows=6 class="form-control" title="product description"
                      v-model="product.description"></textarea>
          </div>
          <button class="btn btn-success pull-right" @click="update"
                  v-show="product.name && product.price>0">
            Update
          </button>
          <!--</form>-->
        </div>
      </div>
    </div>
    <div class="col-md-8 col-md-offset-1">
            <pre>
                {{ product }}
            </pre>
    </div>
  </div>
</template>
<script>
  import swal from 'sweetalert'

  export default {
    created () {
      this.getProduct()
    },
    data () {
      return {
        product: {},
      }
    },
    methods: {
      getProduct () {
        this.$http.get('api/products/' + this.$route.params.product)
          .then(
            response => {
              this.product = response.body
            })
          .catch(
            response => {
              swal(response.status + ' ' + response.statusText, response.body.error, 'error')
            })
      },
      update () {
        this.$http.put('api/products/' + this.$route.params.product,
          this.product)
          .then(response => {
            //                        console.log(response)
            swal('Updated!', 'Your product has been updated!', 'success')
            this.getProduct()
            //                        this.$router.push('/feed')
            this.$router.push('/feed')
            // reload..
          })
      },
    },
  }

</script>



