<template>
  <div class="container">
    <div class="row">
      <div class="col-md-8 col-md-offset-2">
        <div class="panel panel-default">
          <div class="panel-body">
            <form @submit.prevent="create">
              <div class="form-group">
                <label>Name:</label>
                <input name='Name' type="text" class="form-control"
                       data-testing-remove-autofocus="autofocus"
                       ref="name"
                       v-validate="'required'"
                       v-on:keyup.enter="pressedEnter('name')"
                       v-on:keyup.tab="pressedTab('name')"
                       placeholder="Pflichtfeld"
                       v-model="product.name">
                <div class="help-block alert alert-danger"
                     v-show="errors.has('Name')">
                  {{ errors.first('Name') }}
                </div>
              </div>
              <div class="form-group">
                <label>Image:</label>
                <input type="file" class="form-control"
                       @change="imageChanged">
              </div>
              <div class="form-group">
                <label>Price:</label>
                <input name="Preis" type="number" class="form-control" title="price"
                       min="1" max="11" step="0.50" value="6"
                       data-number-to-fixed="2" data-number-stepfactor="5"
                       data-number-calculate-width="false" data-number-fix-only-float="false"
                       data-doc-link="http://vee-validate.logaretm.com/#available-rules"
                       v-on:keyup.enter="pressedEnter('price')"
                       v-on:keyup.tab="pressedTab('price')"
                       v-validate="'max_value:12|min_value:1'"
                       v-model="product.price">
                <div class="help-block alert alert-danger"
                     v-show="errors.has('Preis')">
                  {{ errors.first('Preis') }}
                </div>
              </div>
              <div class="form-group">
                <label>Description:</label>
                <textarea class="form-control" title="description"
                          v-on:keyup.enter="pressedEnter('description')"
                          v-on:keyup.tab="pressedTab('description')"
                          v-model="product.description"></textarea>
              </div>
              <button class="btn btn-success pull-right">
                <!--@click="create">-->
                <!--v-show="product.name && product.price>0"-->

                Create
              </button>
            </form>
          </div>
        </div>
      </div>
      <!--<div class="col-md-8">-->
      <!--<pre>-->
      <!--{{ product }}-->
      <!--</pre>-->
      <!--</div>-->
    </div>
  </div>
</template>
<script>
  import {EventBus} from '@/services/event-bus'

  export default {
    data () {
      return {
        errorsNotHereBCValidation: {},
        product: {
          name: '',
          price: 10,
          description: '',
          //                    user_id: this.$auth.getAuthenticatedUser().id
          image: '',
        },
      }
    },
    created () {
      try {
        EventBus.$emit('notification', {notification: 'create-product component created'})
      } catch (exception) {
        console.log('caught exception: ' + exception)
      }
      // this.$refs['name'].focus()
    },
    beforeDestroy () {
      EventBus.$emit('notification', {notification: 'create-product component beforeDestroy'})
      // EventBus.$off()
    },
    mounted () {
      EventBus.$emit('notification', {notification: 'create-product lifecycle hook :mounted -> focus'})
      // console.log(self.$refs)
      // console.log(self.$refs['name'])
      this.$refs['name'].focus()
    },
    methods: {
      pressedEnter (f) {
        console.log('pressed enter-key in field ' + f)
      },
      pressedTab (f) {
        console.log('pressed tab-key in field ' + f)
      },
      imageChanged (e) {
        console.log(e.target.files[0])
        const fileReader = new FileReader()
        fileReader.readAsDataURL(e.target.files[0])
        fileReader.onload = (e) => {
          this.product.image = e.target.result
        }
        console.log(this.product)
      },
      create () {
        if (this.errors.items.length) {
          console.log(this.errors.items.length)
          this.errors.items.map((e) => console.log(e.msg))
        } else {
          this.$http.post('api/products', this.product)
            .then(response => {
              // console.log(response)
              EventBus.$emit('notification', {
                notification: 'api/products response',
                response: response.body,
              })
              this.$router.push('/feed')
            })
        }
      },
    },
  }

</script>



