<template>
  <div>
    <h4>products</h4>
    <h5>current page: {{ current_page }} </h5>
    <hr/>
    <div class="row">
      <ul>
        <my-product
          v-for="product in products"
          @delete-product="deleteProduct(product)"
          :key="product.id"
          :p_current_user="authenticatedUser"
          :product="product"
          :x_ase_rel="x_ase_rel_product_detail"
          :x_ase_current_page="current_page">
        </my-product>
      </ul>

      <!--<hr/>-->
      <!--<pre>{{ products }}</pre>-->
    </div>
  </div>
</template>
<script>
  import {EventBus} from '@/services/event-bus'
  import swal from 'sweetalert'
  import Product from './Product.vue'

  export default {
    data () {
      return {
        current_page: 0,
        last_page: 0,
        products_from: 0,
        products_to: 0,
        prev_page_visible: false,
        next_page_visible: false,
        products: [],
        x_ase_rel_product_detail: 'product-detail',
      }
    },
    methods: {
      deleteProduct (product) {
        swal({
          title: 'Are you sure?',
          text: 'You will not be able to recover this product',
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#DD6B55',
          confirmButtonText: 'Yes, delete it!',
          closeOnConfirm: false,
        }).then(
          willDelete => {
            if (willDelete) {
              this.$http.delete('api/products/' + product.id)
                .then(response => {
                  console.log(response)
                  let index = this.products.indexOf(product)
                  this.products.splice(index, 1)
                  swal('Deleted!', 'Your product has been deleted.', 'success')
                  EventBus.$emit('notification',
                    {notification: 'product deleted', product: {id: product.id}})
                }).bind(this)
            } else {
              console.log('delete rq nak')
            }
          },
        )
      },
    },
    computed: {
      authenticatedUser () {
        return this.$auth.getAuthenticatedUser()
      }
      ,
    },
    components: {
      'my-product':
      Product,
    },
    // lifecycle hooks:
    // https://vuejs.org/v2/guide/instance.html#Lifecycle-Diagram
    beforeCreate () {
      console.log('lifecycle hook beforeCreate')
    },
    created () {
      EventBus.$emit('notification', {notification: 'products component created'})
      // initialize products
      this.$http.get('api/products', {page: this.current_page}).then(response => {
        this.current_page = response.body.current_page
        this.last_page = response.body.last_page
        this.products_from = response.body.from
        this.products_to = response.body.to
        this.products = response.body.data
        this.next_page_visible = !!response.body.next_page_url
        this.prev_page_visible = !!response.body.prev_page_url
        // this.products = response.body
      })
    },
    // beforeMount
    // mounted
    mounted () {
      EventBus.$emit('notification', {notification: 'products component lifecycle hook created'})
    },
    // beforeUpdate
    // updated  .. both in Mounted State
    // beforeDestroy
    // destroyed .. in Destroyed State
    beforeDestroy () {
      EventBus.$emit('notification', {notification: 'products component beforeDestroy'})
      // EventBus.$off()
    }
    ,
  }
</script>



