<script>

  import {EventBus} from '../services/event-bus'

  // sector control interface
  class LayerOneRefIntercomAdapter {
    // .here: expected: derived from framework layer
    // .here: actual: not implemented
    constructor (ref) {
      this.ref = ref
    }

    onSetRef (reference) {
      this.the_component = reference
    }
  }

  let layerOneRef = new LayerOneRefIntercomAdapter()

  export default {
    components: {},
    created () {
      EventBus.$emit('notification', {notification: 'layer-1 component created'})
    },
    computed: {
      setRef: () => {
        layerOneRef.onSetRef(this)
      },
      processedInputPattern: (influxPattern) => {
        return 'processed: ' + influxPattern
      },

    },

    beforeDestroy () {
      EventBus.$emit('notification', {notification: 'layer-1 component beforeDestroy'})
      //            EventBus.$off()
    }
    ,
  }
</script>
