<template>
  <!-- template-connect.vue -->
  <!-- version {{ version }} -->
  <div class="connect">
    <div>
      <h4>gp Suche: v-{{ version }} / connect-v-{{ computedConnectVersion }}</h4>
      <h5>@ {{ computedTs }}</h5>
    </div>
    <div class="connect__searchPanel">
      <div class="connect__speciesSelect">
        <button @click="handleDogs">Dogs</button>
        <button @click="handleCats">Cats</button>
      </div>

      <div class="connect__search">
        <input type="text"
               :placeholder="searchPlaceholder"
               v-model="searchTerm">
      </div>
    </div>

    <div class="connect__searchResults">
      <div class="connect__result"
           v-for="psychic in filteredResults">

        <div class="connect__resultHeader">
          <img src="http://placekitten.com/g/400/150">
        </div>

        <div class="connect__resultBody">
          <div class="connect__resultRow">
            <span class="connect__name">{{ psychic.name }}</span>
            <em>{{ psychic.price }}</em>
          </div>
          <div class="connect__resultRow">
            <strong>Specialties: </strong>
            <em>{{ psychic.specialties }}</em>
          </div>
          <button v-on:click.prevent="take_gp(psychic.name)">Take me!</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

  export default {

    // should set 'name'
    // template: '#template-connect',

    props: ['connect_version', 'ts'],

    data: function () {
      return {
        version: '1.6',
        dogs: [{
          name: 'Dennis',
          specialties: 'Computers, lost children, squirrels',
          price: 50,
        }, {
          name: 'Daisy',
          specialties: 'Butt smells',
          price: 70,
        }],
        cats: [{
          name: 'Mimi',
          specialties: 'Feathers, mice, 20th century art theft',
          price: 200,
          cares: false,
        }, {
          name: 'Sammy',
          specialties: 'Is actually a dog',
          price: 2,
          cares: true,
        }],
        species: 'dog',
        searchTerm: '',
      }
    },

    created: function () {
      var self = this
      console.log('created: ' + self)
      //                if (null == self.props.ts) {
      //                    self.props.ts = "[created] " + Date()
      //                }
      //                if (null == self.props.connect_version) {
      //                    self.props.connect_version = "in created"
      //                }
    },

    mounted: function () {
      var self = this
      console.log('mounted: ' + self)
    },

    computed: {
      searchPlaceholder: function () {
        'use strict'
        let self = this
        return 'Find a psychic ' + self.species + '.'
      },

      results: function () {
        'use strict'
        var self = this
        self.searchTerm = ''
        if (self.species === 'dog') {
          return self.dogs
        }

        if (self.species === 'cat') {
          return self.cats
        }
      },

      filteredResults: function () {
        'use strict'
        var self = this
        return self.results.filter(function (psychic) {
          return psychic.name.toLowerCase().indexOf(self.searchTerm.toLocaleLowerCase()) !== -1
        })
      },

      computedTs: function () {
        var self = this
        if (self.ts == null) {
          console.log('computedTs')
          return '[computedTs] ' + Date()
        } else return self.ts
      },

      computedConnectVersion: function () {
        var self = this
        if (self.connect_version == null) {
          console.log('computedConnectVersion')
          return '[in computedConnectVersion]'
        } else return self.connect_version
      },
    },

    methods: {
      handleDogs: function (event) {
        'use strict'
        var self = this
        event.preventDefault()
        console.log('handleDogs')
        self.species = 'dog'
      },

      handleCats: function (event) {
        'use strict'
        var self = this
        event.preventDefault()
        console.log('handleCats')
        self.species = 'cat'
      },

      take_gp: function (gp) {
        'use strict'
        var self = this
        //                    if (event) event.preventDefault()
        self.searchTerm = gp
        console.log('take_gp: ' + gp)
        var AseGpEl = document.getElementById('ase_gp')
        if (AseGpEl !== null) {
          AseGpEl.value = gp
        } else {
          console.log('NOP take_gp: no parent AseGpEl')
        }
      },
    },
  }
</script>
<style>
  /*
  red  #E74C3C ;
  active: #007acc;
  "black" #1a1a1a;
  light-grey: #f7f7f7;
  border-color: #d1d1d1;
  input-color: #686868;
  padding top and bottom: 0.625em;
  padding left and right: 0.4375em;
  margin-bottom: 0.5em;
  */
  .connect__searchPanel {
    margin-bottom: 2em;
    padding: 0.75em 0.5em;
    border: 3px solid #1a1a1a;
    border-radius: 2px;
  }

  .connect__speciesSelect {
    margin-bottom: 0.5em;
  }

  .connect__result {
    margin-bottom: 1em;
    background-color: #f7f7f7;
    min-height: 200px;
    border: 1px solid #1a1a1a;
    border-radius: 2px;
  }

  .connect__resultBody {
    padding: 0.625em 0.4375em;
  }

  .connect__name {
    font-size: 1.1875rem;
    font-weight: 900;
  }
</style>



