<template>
  <div class="row">
    <div class="col-md-6 col-md-offset-6">
      <div class="panel panel-default">
        <div class="panel-body">
          <button @click="logout" class="btn btn-success pull-right">
            Logout
          </button>
          <button @click="apiTest" class="btn btn-success pull-right">
            Test
          </button>
        </div>
        <div id="test-user-wrapper">
          <h4>test user</h4>
          <pre>{{ $data.testuser }}</pre>
        </div>
        <div id="test-auth-wrapper">
          <h4>test auth</h4>
          <pre>{{ $data.authobj }}</pre>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        email: '',
        password: '',
        testuser: {
          first_name: 'n/a',
          last_name: 'n/a',
          purpose: 'unknown',
        },
        authobj: null,
      }
    },
    methods: {
      logout () {
        // if (event) {
        //   event.preventDefault()
        // }
        this.$data.authobj = null
        this.$auth.destroyToken()
        this.$router.go('/login')
      },
      apiTest () {
        let self = this
        this.$http.get('api/test')
          .then(response => {
            console.log(response.body.user)
            // this.$data.testuser = response.body.user
            self.testuser = response.body.user
          })
      },
    },
  }
</script>
<style type="text/css"></style>



