<template>
  <div class="row">
    <div class="col-md-6 col-md-offset-6">
      <div class="panel panel-default">
        <div class="panel-body">
          <div class="form-group">
            <input
              v-model="email"
              class="form-control"
              type="email"
              placeholder="Email">
          </div>
          <div class="form-group">
            <input
              v-model="password"
              class="form-control"
              type="password"
              placeholder="Password">
          </div>
          <div>
            <button @click="login" class="btn btn-success pull-right">
              Login
            </button>

            <button @click="apiTest" class="btn btn-success pull-right">
              Test
            </button>
          </div>
        </div>
        <div id="test-user-wrapper">
          <h4>test user</h4>
          <pre>{{ $data.testuser }}</pre>
        </div>
        <div id="test-auth-wrapper">
          <h4>test auth</h4>
          <pre>{{ $data.authobj }}</pre>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        email: '',
        password: '',
        testuser: {
          first_name: 'n/a',
          last_name: 'n/a',
          purpose: 'unknown',
        },
        authobj: null,
      }
    },
    methods: {
      login () {
        // if (event) {
        //   event.preventDefault()
        // }
        let data = {
          client_id: 7,
          client_secret: 'SY33Sry0bTWyaBpTuYr5n9irbn7YANadYhtimajx',
          grant_type: 'password',
          username: this.email,
          password: this.password,
        }
        this.$http.post('oauth/token', data)
          .then(response => {
            //                        console.log(response);
            this.$data.authobj = response.body
            this.$auth.setToken(response.body.access_token,
              response.body.expires_in + Date.now())
            this.$router.go('/feed')
          })
      },
      apiTest () {
        let self = this
        this.$http.get('api/test')
          .then(response => {
            console.log(response.body.user)
            // this.$data.testuser = response.body.user
            self.testuser = response.body.user
          })
      },
    },
  }

</script>
<style type="text/css"></style>



